<!-- eslint-disable -->
<template>
  <div class="slideshow-wrapper">
    <div id="slideshow-home">
      <div class="slide" v-for="s in slides" :key="s.id">
        <div class="svg-container">
          <img-base v-if="s.file == 'chair'" img-name="chair" viewBox="0 0 330 340">
            <img-chair />
          </img-base>

          <img-base v-if="s.file == 'children'" img-name="children" viewBox="0 0 410 340">
            <img-children />
          </img-base>

          <img-base v-if="s.file == 'moon'" img-name="moon" viewBox="0 0 510 340">
            <img-moon />
          </img-base>

          <img-base v-if="s.file == 'frame'" img-name="frame" viewBox="0 0 620 340">
            <img-frame />
          </img-base>
        </div>

        <div class="copy">
          <div class="title">{{ s.title }}</div>
          <div class="text" v-html="s.text"></div>
          <div class="btn white">
            <div class="txt">{{ s.btn_txt }}</div>
          </div>
        </div>
      </div>

      <!-- <div class="arrows-wrapper">
        <div class="arrows">
          <div class="arrow prev" @click="plusSlides(-1)"></div>
          <div class="arrow next" @click="plusSlides(1)"></div>
        </div>
      </div>-->
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import chair from "../../assets/illustrations/final/chair.svg";
import chair2 from "../../assets/illustrations/final/chair.svg";
import ImgBase from "../ImgBase";
import ImgChair from "../illustrations/ImgChair";
import ImgChildren from "../illustrations/ImgChildren";
import ImgMoon from "../illustrations/ImgMoon";
import ImgFrame from "../illustrations/ImgFrame";

export default {
  components: {
    ImgBase,
    ImgChair,
    ImgChildren,
    ImgMoon,
    ImgFrame,
  },
  data() {
    return {
      slideIndex: 1,
      slides: [
        {
          id: "",
          title: "",
          text: "",
          btn_txt: "",
          file: "",
          url: "",
        },
      ],
    };
  },
  methods: {
    bannerAnimate() {
      $("#slideshow-home > div:gt(0)").hide();
      var vm = this;
      vm.drawLines();

      setInterval(function () {
        $("#slideshow-home > div:first")
          .fadeOut(0)
          .next()
          .fadeIn(0)
          .end()
          .appendTo("#slideshow-home");
      }, 7000);
    },
    drawLines() {
      $(".nodules").addClass("start");
      $(".main-path").addClass("start");
      $(".main-path-fill").addClass("start");
    },
    deleteLines() {
      $(".nodules").removeClass("start");
      $(".main-path").removeClass("start");
      $(".main-path-fill").removeClass("start");
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getSlideshow.php")
      .then((resp) => {
        const data = resp.data;
        var slides = [];

        map.call(data, function (d) {
          slides.push({
            id: d.id,
            title: d.title,
            text: d.text,
            btn_txt: d.cta_txt,
            file: d.img,
            url: d.link,
          });
        });

        vm.$data.slides = slides;
      })
      .then(() => {
        vm.bannerAnimate();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: rgba(11, 171, 197, 0.6);

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(65);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.slideshow-wrapper {
  background-color: $white;
  height: 90vh;
  width: 100%;
  position: absolute;
  z-index: 99;

  #slideshow-home {
    height: 100%;
    position: relative;

    .slide {
      width: 100%;
      height: 100%;
      display: flex;
      background-position: 90% 50%;
      background-color: $white;
      align-items: center;
      background-repeat: no-repeat;
      position: absolute;
      transition: 1s;
      background-size: 30%;
      pointer-events: none;
      overflow: hidden;
      justify-content: flex-end;

      @media only screen and (min-width: 992px) and (max-width: 1199px) {
        // justify-content: center;
      }

      @media only screen and (min-width: 768px) and (max-width: 991px) {
        background-position: top;
        background-size: 70% 50%;
        align-items: flex-start;
      }

      @media only screen and (min-width: 600px) and (max-width: 767px) {
        background-position: top;
        background-size: 50% 50%;
        align-items: flex-start;
      }

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        background-position: top;
        background-size: 70% 50%;
        align-items: flex-start;
      }

      // &.active {
      //   opacity: 1;
      //   pointer-events: all;
      // }

      .svg-container {
        width: 50%;
        height: 100%;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        @media only screen and (min-width: 768px) and (max-width: 991px) {
          width: 100%;
          height: 50%;
          align-items: flex-start;
          margin-top: 5%;
        }

        @media only screen and (min-width: 600px) and (max-width: 767px) {
          width: 100%;
          height: 50%;
          align-items: flex-start;
          margin-top: 5%;
        }

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          width: 100%;
          height: 50%;
          align-items: flex-start;
          margin-top: 5%;
        }

        svg {
          width: pxToEm(700);
          height: pxToEm(700);
          transform: scale(0.8);

          @media only screen and (min-width: 1200px) and (max-width: 1920px) {
            transform: scale(1);
          }

          @media only screen and (min-width: 992px) and (max-width: 1199px) {
            transform: scale(1);
          }

          @media only screen and (min-width: 768px) and (max-width: 991px) {
            transform: scale(1);
          }

          @media only screen and (min-width: 600px) and (max-width: 767px) {
            transform: scale(0.7);
          }

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            transform: scale(0.65);
            top: -40px;
            position: absolute;
          }
        }
      }

      .copy {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: pxToEm(600);
        height: pxToEm(440);
        padding: pxToEm(40);
        justify-content: space-between;
        position: absolute;
        left: 7vw;

        @media only screen and (max-width: 1200px) {
          align-items: center;
          text-align: center;
        }

        @media only screen and (min-width: 768px) and (max-width: 991px) {
          left: 50%;
          transform: translateX(-50%);
          bottom: pxToEm(120);
        }

        @media only screen and (min-width: 600px) and (max-width: 767px) {
          left: 50%;
          transform: translateX(-50%);
          bottom: pxToEm(120);
        }

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          left: 50%;
          transform: translateX(-50%);
          bottom: pxToEm(120);
          background-color: rgba(255, 255, 255, 0.8);
        }

        .title {
          color: $turquiose;
          font-size: pxToEm(48);
          font-family: "Dalmatins";

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            font-size: pxToEm(38);
          }
        }

        .text {
          color: $dk-gray-3;
          font-size: pxToEm(28);
          font-family: "OS-Regular";
          line-height: pxToEm(20);
        }

        .btn {
          width: pxToEm(200);
          height: pxToEm(60);

          .txt {
            font-family: "OS-Bold";
            font-size: pxToEm(20);
          }
        }
      }
    }
  }
}
</style>



